import api from '@/api';
export default {
  data() {
    return {
      balance: '0',
      coin: ''
    }
  },
  created() {
    this.getEnsurepool();
  },
  methods: {
    async getEnsurepool() {
      const { data } = await api.getEnsurepool();
      this.balance = data.balance;
      this.coin = data.coin;
      this.startInsuranceTimer();
    },
    startInsuranceTimer() {
      setTimeout(this.getEnsurepool.bind(this), 1000);
    }
  }
}