<script>
    import {Indicator} from "mint-ui";
    import enumMixins from './mixins/enumMixins';
    import countDown from './mixins/countDown';
    import insuranceMixins from './mixins/insuranceMixins';
    import gameIncomeMixins from './mixins/gameIncomeMixins';
    import dataStatisticsMixins from './mixins/dataStatisticsMixins';
    import historyMixins from './mixins/historyMixins';
    // 
    import communityBenefits from './update/communityBenefits';
    import gameRecord from './update/gameRecord';
    import withdrawalApplication from './update/withdrawalApplication';
    import detailedDataMixins from './mixins/detailedDataMixins';
    import api from '@/api';
    import {rechargeToMetaMask, rechargeToken} from './utils/commonHelper';

    import {Toast, MessageBox} from 'mint-ui';
    import moment from 'moment';

    import mydirectPushQuantity from './mixins/mydirectPushQuantity';

    //import { ethers } from "ethers";
    export default {
        name: "App",
        mixins: [enumMixins, countDown, insuranceMixins, gameIncomeMixins, dataStatisticsMixins, historyMixins, communityBenefits, gameRecord, withdrawalApplication, detailedDataMixins, mydirectPushQuantity],
        data() {
            return {
                activeBtn: "recharge",
                ETHActiveBtn: 1,
                joinGameETH: 1,
                lastUseEth: 0,
                isCanJoin: false,
                rechargeValue: 0,
                inviteLink: '',
                inviteNum: '',
                inviterAddress: '',
                exchangeRate: 0,
                ethBalance: 0,
                qcdBalance: 0,
                popupVisible: false,
                historyData: [],
                loadMoreLoading: false,
                activeHistory: '',
                needQcd: 0,
                L: '',
                V: '',
                isShowMyInviter: false,
                //
                profitData: [],
                isprofitData: false,
                //3
                gameRecordData: [],
                isgameRecord: false,
                //
                iswithdrawal: false,
                withdrawalETH: 0,
                withdrawalQCD: 0,

                activeColor: {color: ''},
                popTile: '',
                isskin_pop: false,
                lang: 'zh',
                ge: 0,
                le: 0,
                withdrawalrecord: [],
                iswithdrawalrecord: false,

                //** 
                rechargeQCDValue: 0


            };
        },
        watch: {
            account(newVal) {
                console.log('account', newVal);
                this.getRecommendInfo();
                this.getWallet();
            },
            popupVisible(newVal) {
                if (!newVal) {
                    this.historyData = [];
                    this.contractPageNum = 0;
                    this.sharePageNum = 0;
                    this.selfPageNum = 0;
                }
            }
        },
        mounted() {
            this.getExchangeRate();
            this.getRecommendInfo();
            this.getWallet();
            this.getCanJoinData();
            this.getNeedQcd();
            this.getCustomerLevel();
        },
        methods: {
            /**
             * 
             * @returns {Promise<void>}
             */
            async viewWithdrawalrecord() {
                const params = {
                    address: this.account,
                    pageNum: 0,
                    pageSize: 20
                };
                const {data} = await api.getWithdrawalrecord(params);
                // console.log(JSON.stringify(data));
                this.withdrawalrecord = data.list;
                this.iswithdrawalrecord = true
                // console.log(JSON.stringify(this.withdrawalrecord))
            },

            async getCustomerLevel() {
                const {data} = await api.getCustomerLevel(this.account);
                const {L, V} = data || {};
                this.L = L || 0;
                this.V = V || 0;
            },
            async showMyInviter() {
                this.isShowMyInviter = true;
            },
            async getNeedQcd() {
                const {data} = await api.getNeedQcd(this.account);
                this.needQcd = data;
            },
            ETHChangeHandle(key) {
                this.ETHActiveBtn = key;
                this.joinGameETH = key;
            },
            //
            extractMoney() {

                // Indicator.open();

                const params = new FormData();
                params.append('address', this.account);
                params.append('amount', this.withdrawalETH);

                api.withDrawHandle(params).then(() => {
                    MessageBox('', '');
                    console.log('')
                    this.iswithdrawal = false;
                    this.getSettlementData(); //
                }).catch(err => {
                    MessageBox('', ':' + err.msg);
                    console.log('', err.msg)
                });

            },
            showDataDetail() {

            },
            async inputHandle() {
                this.rechargeValue = this.numFilter(this.rechargeValue);
            },
            /**
             * Qcd
             */
            async inputHandleRecharge() {
                this.rechargeQCDValue = this.numFilter(this.rechargeQCDValue);
            },
            /**
             * Qcd
             */
            rechargeHandle() {
                if (this.rechargeQCDValue == 0.0 || this.rechargeQCDValue == 0) {
                    Toast('QCD');
                    return;
                }
                rechargeToken(this.rechargeQCDValue).then(res => {
                    var txid = res;
                    const params = new FormData();
                    params.append('address', this.account);
                    params.append('amount', this.rechargeQCDValue)
                    params.append('txid', txid)

                    api.setRechargeQcd(params).then(res => {
                        if (res.msg == "ok") {
                            this.getWallet();
                            Toast('');
                        }
                    }).catch(err => {
                        // console.log('err', err)
                        Toast('' + JSON.stringify(err));
                    })
                }).catch(err => {
                    Toast('', err);
                })
            },

            joinGameETHInputHandle() {
                this.joinGameETH = this.setInt(this.joinGameETH);
            },
            setFloat(value) {
                value = String(value);
                value = value.replace(/[^\d.]/g, "");
                value = value.replace(/\.{2,}/g, ".");
                value = value.replace(".", "$#$").replace(/\./g, "").replace("$#$", ".");
                value = value.replace(/^(\-)*(\d+)\.(\d).*$/, '$1$2.$3');
                if (value.indexOf(".") < 0 && value != "") {
                    value = parseFloat(value);
                }
                return value;
            },
            numFilter(value) {
                let realVal = '0'
                if (!isNaN(value) && value !== '') {
                    // 
                    realVal = parseFloat(value).toFixed(1)
                } else {
                    // realVal = '--'
                }
                return realVal
            },
            setInt(value) {
                return parseInt(value);
            },
            async showHistoryRecord() {
                const {data} = await api.getHistoryData();
                console.log(data);
            },
            async getRecommendInfo() {
                const {data} = await api.getRecommendInfo(this.account);
                this.inviteLink = data.inviteLink;
                this.inviteNum = data.inviteNum;
                this.inviterAddress = data.inviterAddress;
            },
            async getExchangeRate() {
                const {data} = await api.getExchangeRate();
                this.exchangeRate = data;
            },
            async getWallet() {
                const {data} = await api.getWallet(this.account);
                this.ethBalance = data.ethBalance;
                this.qcdBalance = data.qcdBalance;
            },
            payHandle() {
                if (!this.rechargeValue || this.rechargeValue == 0) {
                    Toast(`ETH`);
                    return;
                }
                rechargeToMetaMask(this.account, this.rechargeValue).then(res => {
                    this.deposit(res);
                }).catch(err => {
                    console.log(err);
                })
            },
            async deposit(txid) {
                const params = {
                    address: this.account,
                    amount: this.rechargeValue,
                    txid: txid
                };
                await api.deposit(params);
                MessageBox('', '');
            },
            async joinGameHandle() {

                this.getCanJoinData().then(res => {
                    // console.log(''+this.isCanJoin+' ge:'+this.ge+'  le:'+this.le)
                    if (this.isCanJoin && (this.ge <= this.joinGameETH <= this.le)) {
                        rechargeToMetaMask(this.account, this.joinGameETH).then(res => {
                            this.attendRecord(res);
                            // this.getWallet();
                        }).catch(err => {
                            console.log(err);
                        })
                    } else {
                        if (!this.joinGameETH) {
                            Toast(`ETH`);
                            return;
                        }
                        if (!this.isCanJoin) {
                            MessageBox('', '');
                            return;
                        }
                        if (this.joinGameETH <= this.lastUseEth) {
                            MessageBox('', 'eth');
                            return;
                        }
                    }
                }).catch(err => {

                })


            },

            async getCanJoinData() {
                const {data} = await api.isCanJoin(this.account);
                // this.lastUseEth = data.lastAmount;
                this.ge = data.ge;
                this.le = data.le;
                this.isCanJoin = data.isOut;
                if (!this.isCanJoin) {
                    this.activeColor.color = '#82D900'
                }
            },
            async attendRecord(txid) {
                const params = {
                    address: this.account,
                    amount: this.joinGameETH,
                    txid: txid
                };
                await api.attendRecord(params);

                MessageBox('', '');
            },
            copyHandle(value) {
                const input = document.createElement('input');
                document.body.appendChild(input);
                input.setAttribute('value', value);
                input.select();
                if (document.execCommand('copy')) {
                    document.execCommand('copy');
                }
                document.body.removeChild(input);
                Toast({
                    message: '',
                    iconClass: 'icon icon-success'
                });
            },


            loadMore() {
                this.loadMoreLoading = true;
                switch (this.activeHistory) {
                    case 'contract':
                        this.loadMoreContract();
                        break;
                    case 'share':
                        this.loadMoreShareData();
                        break;
                    case 'self':
                        this.loadMoreSelfData();
                        break;
                }
            },
            //
            showskin_pop() {
                this.isskin_pop = true;
            },
            hideskin_pop() {
                this.isskin_pop = false;
            },
            changeLanguage(lang) {
                this.$i18n.locale = lang;
                this.lang = lang;
            }
        },
        computed: {
            exchangeTotalQCD() {
                const rechargeValue = isNaN(+this.rechargeValue) ? 0 : +this.rechargeValue;
                const exchangeRate = isNaN(+this.exchangeRate) ? 0 : +this.exchangeRate;
                return rechargeValue * exchangeRate;
            },
            account() {
                return this.$store.state.account;
            },
            formatDate() {
                return (date) => moment(date).format('YYYY-MM-DD HH:mm:ss');
            },
            accountHandle() {
                let str = String(this.account);
                let sta = str.substring(0, 4);
                let ena = str.substring(str.length - 4, str.length);
                let ss = sta + '****' + ena;
                return ss
            },
            inviterAddressHandle() {
                if (this.inviterAddress) {
                    let str = String(this.inviterAddress);
                    let sta = str.substring(0, 4);
                    let ena = str.substring(str.length - 4, str.length);
                    let ss = sta + '****' + ena;
                    return ss
                }
                return ''
            },
            joinBtns() {
                return [
                    {name: this.$t('joinBtns.name1'), key: "game"},
                    {name: this.$t('joinBtns.name2'), key: "recharge"},
                    {name: this.$t('joinBtns.name3'), key: "share"},
                    {name: this.$t('joinBtns.name5'), key: "recharge1"},
                    {name: this.$t('joinBtns.name4'), key: "rule"}
                ]
            }

        },
        components: {
            // HelloWorld
        }
    };
</script>

