                .sub-title {
                    margin: @marginWidth*4 0;
                    font-size: 30px;
                    text-align: left;
                    color: #fff;
                }

                table {
                    width: 100%;
                    border: 1px solid #fff;
                    border-collapse: collapse;

                    td {
                        font-size: 18px;
                        padding: @paddingWidth*1.5 @paddingWidth;
                        text-align: center;
                    }
                }

                p {
                    font-size: 24px;
                    padding: 3px 0;
                    color: rgba(255, 255, 255, 0.7);

                    span {
                        font-size: 22px;
                    }

                    &.tr {
                        text-indent: 2em;
                    }
                }
            }

            .share {
                margin-top: @marginWidth * 2;

                .join-container {
                    padding: 0;
                }
            }

            .button-text-container {
                .flex-center;
                justify-content: space-between;
                color: rgba(19, 181, 177, 1);
                font-size: 25px;
                background-color: @contentBg;
                border-radius: 20px;
                margin-bottom: @marginWidth * 2;

                .text {
                    flex: 1;
                    overflow: hidden;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    // .flex-center;
                    // justify-content: flex-end;
                    padding: 10px @paddingWidth * 4;
                    text-align: right;

                    img {
                        width: 40px;
                    }

                    &.column {
                        flex-direction: column;
                        align-items: flex-end;
                    }

                    &.customer {
                        span {
                            padding-right: @paddingWidth * 4;
                            padding-left: @paddingWidth * 1.5;
                            color: #fff;
                            font-size: 32px;
                        }
                    }
                }

                &.reverse {
                    flex-direction: row-reverse;

                    .text {
                        justify-content: flex-start;
                    }
                }
            }

            .sub-title {
                width: 100%;
                text-align: center;
                margin: @marginWidth*1.5 0;
                color: rgba(19, 181, 177, 0.8);
            }
        }
    }

    .bottom-img {
        margin-top: @marginWidth * 3;
        width: 100%;
    }

    .history-container {
        width: 750px;
        height: 500px;
        overflow-y: auto;

        &.inviter {
            height: 300px;
        }

        .no-data {
            .flex-column-center;
            height: 100%;

            h1 {
                color: gray;
            }
        }
    }

    .popup-container {
        width: 750px;
        height: 500px;
        overflow-y: auto;
        background-color: #062c38;
    }

    .rg-table-box::-webkit-scrollbar {
        width: 10px;
        height: 2px;
    }

    .rg-table-box::-webkit-scrollbar-track {
        /**/
        box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
        background: #ededed;
        border-radius: 10px;
    }

    .rg-table-box::-webkit-scrollbar-thumb {
        /**/
        border-radius: 10px;
        background-color: #2e75d3;
        background-image: -webkit-linear-gradient(45deg,
        rgba(255, 255, 255, 0.2) 25%,
        transparent 25%,
        transparent 50%,
        rgba(255, 255, 255, 0.2) 50%,
        rgba(255, 255, 255, 0.2) 75%,
        transparent 75%,
        transparent);
    }

    .withdrawalinput {
        color: #fff;
        border: none;
        background: none;
        width: calc(100% - 90px);
        height: 100%;
        padding-left: @paddingWidth * 2;
        overflow: hidden;
        -moz-appearance: textfield;
        -webkit-appearance: none;

        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
            -webkit-appearance: none;
        }

        &:focus {
            border: none;
            outline: none;
        }
    }

    .withdrawalbutton {
        border: 2px solid @activeColor; /*no*/
        padding: @paddingWidth * 2 @paddingWidth * 4;
        border-radius: 20px 0px 0px 20px;
        height: 100%;
        color: #000;
    }

</style>

