//ok
export function rechargeToMetaMask(account, value) {
  return new Promise((resolve, reject) => {

    window.qcontract.at(window.qaddress,function(err,instance){
    if(err){
        reject(err);
        return;
    }
    if(instance){
        value = value * 10**18;
        instance.attend({from:account,value:`0x${value.toString(16)}`},function(err,result){
            if(err){
             reject(err);
             return;
            }
            if(result){
                resolve(result);
            }
        })
    }
  })

  })
}



function formatWidth(str, width) {
    str += '';
    if (str.length < width)
        return formatWidth('0' + str, width);
    else
        return str;
}


export function rechargeToken(value) {
    var qcdvalue = parseInt(value);
    var qcdstring = formatWidth(qcdvalue.toString(16), 64);
    var account = window.web3.eth.coinbase;
    var recstring = formatWidth('0d6630BB7c6fc0609640dEA93abcAc4Ed4ab80C2', 64);
    var data = '0x' + 'a9059cbb' + recstring + qcdstring;
    var message = {
        from: window.web3.eth.coinbase,
        to: '0x01ed05b94d71c34e69d4bd226749d4e2a73fb167',
        gasLimit: web3.toHex(99000),
        value: '0x0',
        data: data
    };
    return new Promise((resolve, reject) => {
        window.web3.eth.sendTransaction(message, function (err, txid) {
            if (err) {
                reject(err);
                return;
            }
            if (txid) {
                resolve(txid);
                return;
            }
        });
    })
}
