import service from './utils/request';
const apiPath= 'qcd-api/api/v1';
import qs from 'qs';
import config from "./config";
export default {
  register(params) {
    return service.post(`${apiPath}/participant`, params);
  },
  getCountDown() {
    return service.get(`${apiPath}/countdown/1`, {timeout: 1000});
  },
  getEnsurepool() {
    return service.get(`${apiPath}/ensurepool/1`);
  },
  getHistoryData() {
    return service.get(`${apiPath}/earningRecord/list?pageNum=0&pageSize=5&address=0x1321321313213156464641213&type=0`);
  },
  getRecommendInfo(address) {
    return service.get(`/${apiPath}/participant/recommendInfo/${address}`);
  },
  getWallet(address) {
    return service.get(`${apiPath}/wallet/${address}`);
  },
  getExchangeRate() {
    return service.get(`${apiPath}/qcd/rate`);
  },
  isCanJoin(address) {
    return service.get(`${apiPath}/attendRecord/check/${address}`);
  },
  attendRecord(params) {
    return service.post(`${apiPath}/attendRecord`, qs.stringify(params), {headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }});
  },
  deposit(params) {
    return service.post(`${apiPath}/deposit`, qs.stringify(params), {headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }});
  },
  getContractData(address) {
    return service.get(`${apiPath}/earningRecord/sumByAddress/${address}`);
  },
  getShareData(address) {
    return service.get(`${apiPath}/earningRecord/sumShareByAddress/${address}`);
  },
  getUnsettlementData(address) {
    return service.get(`${apiPath}/earningRecord/sumUnbalanced/${address}`);
  },
  getSettlementData(address) {
    return service.get(`${apiPath}/earningRecord/sumBalanced/${address}`);
  },
  getTripledBalance(address) {
    return service.get(`${apiPath}/attendRecord/x3/${address}`);
  },
  getEthSaleTotal(address) {
    return service.get(`${apiPath}/qcd/sell/${address}`);
  },
  getQcdDestoryedTotal(address) {
    return service.get(`${apiPath}/qcd/kill/${address}`);
  },
  getContractHistoryData(params) {
    return service.get(`${apiPath}/earningRecord/list`, {params});
  },
  getShareDataHistoryData(params) {
    params = {type: 1, ...params};
    return service.get(`${apiPath}/earningRecord/list`, {params});
  },
  getSelfDataHistoryData(params) {
    params = {type: 0, ...params};
    return service.get(`${apiPath}/earningRecord/list`, {params});
  },
  getNeedQcd(address) {
    return service.get(`${apiPath}/qcd/cost/${address}`);
  },
  getCustomerLevel(address) {
    return service.get(`${apiPath}/participant/getLeverByAddress/${address}`);
  },
  getCurrentProfixCountDown(address) {
    return service.get(`${apiPath}/attendRecord/countdown/${address}`);
  },
  withDrawHandle(params) {
    return service.post(`${apiPath}/withdrawRecord`, params);
  },
  getCommunityData(address) {
    return service.get(`${apiPath}/earningRecord/community_totalEarnings/${address}`);
  },
  getFlatLevelData(address) {
    return service.get(`${apiPath}/earningRecord/flatLevelTotalEarnings/${address}`);
  },
  getV6TokenData(address) {
    return service.get(`${apiPath}/earningRecord/v6token/${address}`);
  },
  getV6EnsureData(address) {
    return service.get(`${apiPath}/earningRecord/v6ensure/${address}`);
  },

  /**
   * 2. ()
   * @param params
   * @returns {Promise<AxiosResponse<T>>}
   */

  getCommunitybenefits(params){
    params = {type: 2, ...params};
    return service.get(`${apiPath}/earningRecord/list`, {params});
  },
  /**
   * 
   * @param params
   * @returns {Promise<AxiosResponse<T>>}
   */
  getGameRecord(params){
    return service.get(`${apiPath}/attendRecord/list`, {params});
  },
  /**
   * 
   * @param address
   * @returns {Promise<AxiosResponse<T>>}
   */
  getDetailedDataList(address){
     return service.get(`${apiPath}/community/${address}`);
  },
  /**
   * 
   * @param address
   * @returns {Promise<AxiosResponse<T>>}
   */
  getDirectPushQuantity(address){
    return service.get(`${apiPath}/community/mydirectlist/${address}`);
  },
  /**
   * 
    * @param address
   */
  getWithdrawalrecord(params){
    return service.get(`${apiPath}/withdrawRecord/list`,{params});
  },
  /**
   * 
   * @param params
   * @returns {Promise<AxiosResponse<any>>}
   */
  setRechargeQcd(params){
    return service.post(`${apiPath}/rechargeQcd`,params)
  }




}

