import api from '@/api';
export default {
    data() {
        return {
            pageNum: 0,
            pageSize: 20,
        }
    },
    methods: {
        async gameRecord() {
            const params = {
                pageNum: this.pageNum,
                pageSize: this.pageSize,
                address: this.account
            };
            const {data} = await api.getGameRecord(params);
            this.gameRecordData = data.list
            // console.log(JSON.stringify(data.list))
            this.isgameRecord = true;
        }
    },
    computed: {
        account() {
            return this.$store.state.account;
        }
    }
}