import api from '@/api';
export default {
  data() {
    return {
      H: "00",
      M: "00",
      S: "00",


      totalSec: 0,
      countDownTimer: null,

    }
  },
  created() {
    this.getCountDown(true);

  },
  methods: {
    getCountDown(isFirst = false) {
      const startTime = +new Date();
      api.getCountDown().then(res => {
        const endTime = +new Date();
        const { data: {hour, min, sec} } = res;

        this.setTotalSec(hour, min, sec);
        if (isFirst) {
          this.manualCountDown(true);
        } else {
          this.countDownTimer = setTimeout(this.manualCountDown.bind(this), 1000 - (endTime - startTime));
        }
      }).catch(() => {
        this.manualCountDown();
      })
    },
    manualCountDown(isFirst = false) {
      if (this.totalSec <= 0) return;
      isFirst || this.totalSec--;
      const H = Math.floor(this.totalSec / 60 / 60);
      const M = Math.floor(this.totalSec / 60 - 60 * H);
      const S = Math.floor(this.totalSec - (60 * 60 * H + M * 60));
      this.H = H < 10 ? `0${H}` : H;
      this.M = M < 10 ? `0${M}` : M;
      this.S = S < 10 ? `0${S}` : S;
      this.getCountDown();
    },
    setTotalSec(H,M,S) {
      this.totalSec = +H*60*60+(+M*60)+(+S);
    }
  },





  computed: {
  },
  destroyed() {
    clearTimeout(this.countDownTimer);
  }
}