import api from '@/api';
export default {
  data() {
    return {
      contractData: 0,
      shareData: 0,
      unsettlementEth: 0,
      unsettlementQcd: 0,
      settlementEth: 0,
      settlementQcd: 0,
      communityData: {},
      flatData: {},
      v6TokenData: {},
      v6EnsureData: {}
    }
  },
  created() {
    this.getContractData();
    this.getShareData();
    this.getUnsettlementData();
    this.getSettlementData();
    this.getCommunityData();
    this.getFlatLevelData();
    this.getV6TokenData();
    this.getV6EnsureData();
  },
  methods: {
    async getContractData() {
      const { data } = await api.getContractData(this.account);
      // console.log('data:'+JSON.stringify(data))
      this.contractData = data.amount;
    },
    async getShareData() {
      const { data } = await api.getShareData(this.account);
      this.shareData = data.amount;
    },
    async getUnsettlementData() {
      const { data } = await api.getUnsettlementData(this.account);
      this.unsettlementEth = data.eth;
      this.unsettlementQcd = data.qdao;
    },
    async getSettlementData() {
      const { data } = await api.getSettlementData(this.account);
      this.settlementEth = data.eth;
      this.settlementQcd = data.qdao;
    },
    async getCommunityData() {
      const { data } = await api.getCommunityData(this.account);
      // console.log('data',JSON.stringify(data))
      this.communityData = data.amount;
    },
    async getFlatLevelData() {
      const { data } = await api.getFlatLevelData(this.account);
      this.flatData = data;
    },
    async getV6TokenData() {
      const { data } = await api.getV6TokenData(this.account);
      this.v6TokenData = data;
    },
    async getV6EnsureData() {
      const { data } = await api.getV6EnsureData(this.account);
      this.v6EnsureData = data;
    }
  },
  computed: {
    account() {
      return this.$store.state.account;
    }
  }
}