import api from '@/api';

export default {
    data() {
        return {}
    },
    methods: {
        async withdrawal() {
            this.withdrawalETH=0;
            this.withdrawalQCD=0;
            this.iswithdrawal = true;
        },
        async withdrawalChange(){
            var QCD = 0;
            if (this.withdrawalETH != 0) {
                QCD = this.withdrawalETH * 0.03 * 10000;
                this.withdrawalQCD = QCD.toFixed(2);
            }
        }
    },
    computed: {
        account() {
            return this.$store.state.account;
        }
    }
}