import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    account: '',
    rate: '',
    caddress: ''
  },
  mutations: {
    setAccount(state, data) {
      state.account = data;
    },
    c(state, data) {
      state.rate = data;
    },
    setCAddress(state, data) {
      state.caddress = data;
    },
    setTokenAddress(state, data){
      state.qcdtokenaddress = data;
    }
  }
});

export default store;