import api from '@/api';

export default {
    data() {
        return {
            contractPageNum: 0,
            contractPageSize: 20,
            contractTotal: 0,
            sharePageNum: 0,
            sharePageSize: 20,
            shareTotal: 0,
            selfPageNum: 0,
            selfPageSize: 20,
            selfTotal: 0
        }
    },
    methods: {
        async showContractList(isLoadMore = false) {
            const params = {
                pageNum: this.contractPageNum,
                pageSize: this.contractPageSize,
                address: this.account,
                type: 0
            };
            this.activeHistory = 'contract';
            const {data} = await api.getContractHistoryData(params);
            isLoadMore || (this.popupVisible = true);
            this.historyData = isLoadMore ? [...this.historyData, ...data.list] : data.list;
            // console.log('list:',data.list.length)
            this.contractTotal = data.total;
            this.popTile = this.$t("common.hysy")
        },
        async loadMoreContract() {
            if (this.historyData.length < this.contractTotal) {
                this.contractPageNum++;
                await this.showContractList(true);
                this.loadMoreLoading = false;
            }
        },
        async showShareList(isLoadMore = false) {
            const params = {
                pageNum: this.sharePageNum,
                pageSize: this.sharePageSize,
                address: this.account
            };
            this.activeHistory = 'share';
            const {data} = await api.getShareDataHistoryData(params);
            isLoadMore || (this.popupVisible = true);
            this.historyData = isLoadMore ? [...this.historyData, ...data.list] : data.list;
            this.shareTotal = data.total;
            this.popTile = this.$t("common.fxsy");
        },
        async loadMoreShareData() {
            if (this.historyData.length < this.shareTotal) {
                this.sharePageNum++;
                await this.showShareList(true);
                this.loadMoreLoading = false;
            }
        },
        async showSelfList(isLoadMore = false) {
            const params = {
                pageNum: this.selfPageNum,
                pageSize: this.selfPageSize,
                address: this.account
            };
            this.activeHistory = 'self';
            const {data} = await api.getSelfDataHistoryData(params);
            isLoadMore || (this.popupVisible = true);
            this.historyData = isLoadMore ? [...this.historyData, ...data.list] : data.list;
            this.selfTotal = data.total;
            this.popTile = this.$t("common.sydjs")
        },
        async loadMoreSelfData() {
            if (this.historyData.length < this.selfTotal) {
                this.selfPageNum++;
                await this.showSelfList(true);
                this.loadMoreLoading = false;
            }
        }


    }
}