<template>
    <div id="app">
        <Home v-if="isRelationMetaMask"/>
        <div class="no-realtion" v-else>
            <h1>{{msg}} </h1>
        </div>
    </div>
</template>

<script>
    // import { Indicator } from "mint-ui";
    //import { ethers } from "ethers";
    import configqcd from './utils/configqcd';
    import configtoken from "./utils/configtoken";
    import {getUrlParamsValue} from './utils/commonHelper';
    import Home from './Home';
    import api from '@/api';
    import {Toast} from 'mint-ui';
    import {mapMutations} from 'vuex';

    export default {
        name: "App",
        data() {
            return {
                isRelationMetaMask: false,
                account: '',
                msg: ' Meta Mask'
            };
        },
        created() {
            this.initWeb3();
        },
        methods: {
            ...mapMutations(['setAccount', 'setCAddress', 'setTokenAddress']),
            checkAccount() {
                var _this = this;
                _this.account = window.web3.eth.coinbase;
                console.log("checkaccount user:" + _this.account);
                setInterval(function () {
                    if (_this.account != window.web3.eth.coinbase) {
                        _this.account = window.web3.eth.coinbase;
                        window.location.reload();
                    }
                }, 1000);
            },
            async initWeb3() {
                if (typeof window.ethereum !== 'undefined' || (typeof window.web3 !== 'undefined')) {
                    // Web3 browser user detected. You can now use the provider.
                    const provider = window['ethereum'] || window.web3.currentProvider
                    if (provider.isMetaMask || provider.isImToken) {
                        await provider.enable();
                        if(provider.networkVersion && provider.networkVersion != 1){
                            this.msg=''
                            return;
                        }
                        // if (provider.networkVersion && provider.networkVersion != 3) {
                        //     this.msg = 'Ropsten'
                        //     return;
                        // }
                        this.account = window.web3.eth.coinbase;

                        this.setAccount(this.account);
                        this.register(this.account);
                        this.checkAccount();
                        this.setCAddress(configqcd.contractAddress);
                        this.setTokenAddress(configtoken.contractAddress);
                        window.qcontract = window.web3.eth.contract(configqcd.abi, configqcd.contractAddress);
                        window.qaddress = configqcd.contractAddress;
                        window.qcdtoken = window.web3.eth.contract(configtoken.abi, configtoken.contractAddress);
                        window.qcdtokenaddress = configtoken.contractAddress;
                    } else {
                        console.log('Non-Ethereum browser detected. You should consider trying MetaMask!');
                    }
                } else {
                    console.log('eth')
                }
            },
            async register(address) {
                if (!address) return;
                const inviterAddress = getUrlParamsValue('inviterAddress');
                if (!inviterAddress) {
                    this.msg = '';
                } else {
                    const params = new FormData();
                    params.append('address', address);
                    params.append('inviterAddress', inviterAddress);
                    api.register(params).then(() => {
                        this.isRelationMetaMask = true;
                        console.log(', ');
                    }).catch((error) => {
                        Toast(error.msg);
                    });
                }
            }
        },
        components: {
            Home
        }
    };
</script>

<style lang="less" scoped>
    @import './assets/common.less';

    #app {
        height: 100%;
    }

    .no-realtion {
        height: 100%;
        .flex-center
    }
</style>
