import api from '@/api';
//  2. ()
export default {
    data() {
        return {
            pageNum: 0,
            pageSize: 20,
        }
    },
    methods: {
        async detailed() {
            const params = {
                pageNum: this.pageNum,
                pageSize: this.pageSize,
                address: this.account
            };
            const {data} = await api.getCommunitybenefits(params);
            this.profitData = data.list
            this.isprofitData = true
        }
    },
    computed: {
        account() {
            return this.$store.state.account;
        }
    }
}