import api from '@/api';

export default {
    data() {
        return {
            detailedData: {},
            isViewDetaile: false
        }
    },
    created() {

    },
    methods: {
        async getDetailedDataList() {
            const {data} = await api.getDetailedDataList(this.account);
            // console.log('getDetailedDataList:'+JSON.stringify(data))
            this.detailedData = data;
            this.isViewDetaile = true
        }
    },
    computed: {
        account() {
            return this.$store.state.account;
        }
    }

}