import api from '@/api';
export default {
    data() {
        return {
            directPushQuantity: [],
            isdirectPushQuantity: false
        }
    },
    created() {
    },
    methods: {
        async getDirectPushQuantity() {
            const {data} = await api.getDirectPushQuantity(this.account);
            this.directPushQuantity = data
            this.isdirectPushQuantity = true;
        },
        formatAddress(address){
            if(address){
                let str = String(address);
                let sta = str.substring(0, 4);
                let ena = str.substring(str.length - 4, str.length);
                let ss = sta + '****' + ena;
                return ss
            }
            return "";
        }
    },
    computed: {
        account() {
            return this.$store.state.account;
        }
    }
}