import api from '@/api';
import moment from "moment";

export default {
    data() {
        return {
            tripledBalance: '',
            saleTotal: '',
            destoryedTotal: '',
            currentH: '00',
            currentM: '00',
            currentS: '00',
            revenueCountdownTimer: null,
            countdownTime: '00:00:00'
        }
    },
    created() {
        this.getTripledBalance();
        this.getEthSaleTotal();
        this.getQcdDestoryedTotal();
        // this.getCurrentProfixCountDown();
        this.startCountdown()
    },
    methods: {
        async getTripledBalance() {
            const {data} = await api.getTripledBalance(this.account);

            this.tripledBalance = data;

        },
        async getEthSaleTotal() {
            const {data} = await api.getEthSaleTotal(this.account);
            this.saleTotal = data;
        },
        async getQcdDestoryedTotal() {
            const {data} = await api.getQcdDestoryedTotal(this.account);
            this.destoryedTotal = data;
        },
        // 
        getCurrentProfixCountDown() {

            if (this.countdownTime == '00:00:00' || '') {
                api.getCurrentProfixCountDown(this.account).then(res => {
                    const {data} = res;
                    const {hour, min, sec} = data || {};
                    var d = new Date();
                    d.setHours(hour);
                    d.setMinutes(min);
                    d.setSeconds(sec);
                    if (hour == 0 && min == 0 && sec == 0) {
                        clearTimeout(this.revenueCountdownTimer)
                    } else {
                        this.countdownTime = this.getTimeLeft(d.getTime())
                    }
                }).catch(err => {
                    // console.log(err.success)
                    if (!err.success) {
                        clearTimeout(this.revenueCountdownTimer)
                    }
                })
            } else {
                var d = new Date();
                var hour = this.countdownTime.split(":")[0];
                var min = this.countdownTime.split(":")[1];
                var sec = this.countdownTime.split(":")[2];
                d.setHours(hour);
                d.setMinutes(min);
                d.setSeconds(sec);
                this.countdownTime = this.getTimeLeft(d.getTime())
            }
        },

        startCountdown() {
            var that = this;
            this.revenueCountdownTimer = setInterval(function () {
                that.getCurrentProfixCountDown()
            }, 1200);

        },
        getTimeLeft(time) {
            var s = moment(time).subtract(1, 'seconds').format('HH:mm:ss');
            return s
        }

    },
    computed: {
        account() {
            return this.$store.state.account;
        }
    }
}